.resume {
}

.splitScreen {
    display: flex;
    flex-direction: row;
}

.column {
    width: 50%;
    padding: 2px 1vw;
}

.sectionTitle {
    color: #848889;
    line-height: 0;
    text-align: left;
}

.sectionContent {
    color: #494f5c;
    margin: 0;
    padding-left: 10px;
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    flex-wrap: wrap;
    font-size: calc(10px + 1vmin);
}
